.container-background {
  width: 300px;
  height: 150px;
  color: red;
  display: flex;
}

.glossy-effect {
  text-align: center;
  color: #fff;
  background: linear-gradient(145deg, rgba(255, 255, 255, .5), rgba(255, 255, 255, .1)), rgba(0, 140, 61, .8);
  border-radius: 8px;
  padding: 20px;
  font-size: 20px;
  animation: shine 2s linear infinite;
  box-shadow: inset 0 0 10px rgba(255, 255, 255, .5), 0 0 20px rgba(0, 140, 61, .7);
}

@keyframes shine {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 100%;
  }
}

.water-effect {
  width: 300px;
  height: 150px;
  background: rgba(0, 140, 61, .8);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 140, 61, .5);
}

.water-effect:before {
  content: "";
  width: 200%;
  height: 200%;
  opacity: .5;
  background: radial-gradient(circle, rgba(255, 255, 255, .5), rgba(255, 255, 255, 0) 70%);
  animation: water-shine 5s infinite;
  position: absolute;
  top: -100px;
  left: -100px;
  transform: rotate(45deg);
}

@keyframes water-shine {
  0% {
    transform: translate(-50%, -50%)rotate(45deg);
  }

  100% {
    transform: translate(50%, 50%)rotate(45deg);
  }
}

.water-effect2 {
  text-align: center;
  background: rgba(0, 140, 61, .8);
  border-radius: 8px;
  padding: 20px;
  font-size: 20px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(0, 140, 61, .7);
}

.water-effect2:before {
  content: "";
  width: 200%;
  height: 100%;
  opacity: .6;
  pointer-events: none;
  background: radial-gradient(circle, rgba(255, 255, 255, .3) 20%, rgba(0, 0, 0, 0) 80%);
  animation: waterMove 3s linear infinite;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)scale(1.5);
}

@keyframes waterMove {
  0% {
    transform: translateX(-50%)translateY(0)scale(1.5);
  }

  50% {
    transform: translateX(-50%)translateY(-15px)scale(1.7);
  }

  100% {
    transform: translateX(-50%)translateY(0)scale(1.5);
  }
}

